<template>
  <cube-page type="form-custom" :show-title="initParams.showTitle" :title="title" :style="(initParams.showTitle||initParams.showToolbar)?'height:calc(100% - 44px);':'height:calc(100%)'">
    <div slot="content" :style="(initParams.showTitle&&initParams.showToolbar)?'padding-bottom:46px':'padding-bottom:2px'">
      <cube-form :model="activeModel" @validate="validateHandler" @submit="submitHandler">
        <cube-form-group>
          <cube-form-item v-for="(f,index) in activeField" v-bind:key="index" :field="f">
          </cube-form-item>
        </cube-form-group>
      </cube-form>

      <cube-toolbar v-if="initParams.showToolbar" :actions="actions" @click="clickHandler"></cube-toolbar>
      <cube-drawer
        ref="drawer"
        :data="data"
        :selected-index="selectedIndex"
        @change="changeHandler"
        @select="selectDrawerHandler"
        @cancel="cancelHandle"
      >
        <span slot="title">文档列表</span>
        <cube-drawer-panel v-for="(panel, index) in data" :key="index" :index="index" :data="panel">
          <cube-drawer-item v-for="(item, i) in panel" :item="item" :key="i" :index="i">
            <!-- <i class="cubeic-round-border"></i> -->
            <span>{{item.text}}</span>
          </cube-drawer-item>
        </cube-drawer-panel>
      </cube-drawer>
    </div>
  </cube-page>
</template>

<script type="text/ecmascript-6">
import { format } from "../utils/date-extension";
import { num2Chinese , encodeUrl} from "../utils/string-extension";
import CubePage from "../components/cube-page.vue";
import CubeButtonGroup from "../components/cube-button-group.vue";
import DatePicker from "../components/date-picker.vue";
const defaultToolbaraActions = [
  {
    text: '<span style="color:#464646;font-weight:800;font-size:1.05em;">文档列表</span>',
    action: "showDrawer",
  },
  {
    text: '<span style="color:#464646;font-weight:800;font-size:1.05em;">提交</span>',
    action: "submitHandler",
  },
];

const ignoreToolbaraActions = [
  {
    text: '<span style="color:#464646;font-weight:800;font-size:1.05em;">提交</span>',
    action: "submitHandler"
  },
];

const baseUrl = (window.options&&window.options.baseUrl)||"http://localhost:8828/api/"
const getDocApiUrl = (window.options&&window.options.getDocMethod)||"getDocInfoById/"
const addPatientDocApiUrl = (window.options&&window.options.addPatientDocMethod)||"addPatientDoc"
const getPatientDocApiUrl = (window.options&&window.options.getPatientDocMethod)||"getPatientDocInfoById/"

const DATE = {
  props: {
    value: {
      type: String,
      default() {
        return null
      }
    },
    placeholder: {
      type: String,
      default() {
        return '请选择'
      }
    }
  },
  data() {
    return {
      selected: null
    }
  },
  render(createElement) {
    return createElement('cube-button', {
      on: {
        click: this.showPicker
      }
    }, this.selected || this.value || this.placeholder)
  },
  mounted() {
    this.picker = this.$createDatePicker({
      title: "日期时间",
      min: new Date(2008, 7, 8, 8, 0, 0),
      max: new Date(2030, 9, 20, 20, 59, 59),
      value: new Date(),
      columnCount: 5,
      onSelect: this.selectHandler,
    });
    // console.log('load date',this.value)
  },
  methods: {
    showPicker() {
      this.picker.show()
    },
    selectHandler(date, selectedIndex, selectedTxt) {
      let val = format(date,'yyyy-MM-dd HH:mm');
      this.selected = val
      this.$emit('input', val)
    }
  }
}
export default {
  data() {
    return {
      loading: null,
      initParams: {
        showToolbar: false,
        showTitle: false,
        planId: "",
        patientDocId: "",
        docId: "",
        version: 0,
        patientId: "",
        Online: true
      },
      fields: {},
      activeModel:{},
      activeField: [],
			doc: {},
			plans: {},
      dateTimePicker: null,
      validity: {},
      valid: undefined,
      models: [],
      submiting: false,
      selectedIndex: [],
      data: [[], []],
      computeFields: {}
    };
  },
  computed: {
    actions() {
      if (this.initParams.showToolbar) {
        return defaultToolbaraActions;
      } else {
        return ignoreToolbaraActions;
      }
    },
    isReadyOnly() {
      return !(this.initParams.patientDocId || this.initParams.patientId);
    },
    getDocUrl() {
      let r = this.isReadyOnly;
      // console.log('readyonly=',r)
      if (r) {
        return this.initParams.patientId?(baseUrl+getDocApiUrl+"?docId="+this.initParams.docId):(baseUrl + getDocApiUrl + this.initParams.docId);
      } else {
        return this.initParams.patientId?(baseUrl+getDocApiUrl+"?docId="+this.initParams.docId+"&patientId="+this.initParams.patientId):(baseUrl + getPatientDocApiUrl + this.initParams.patientDocId);
      }
    },
    saveUrl() {
      if (this.isReadyOnly) {
        return baseUrl + addPatientDocApiUrl
      } else {
        return baseUrl + addPatientDocApiUrl
      }
    },
    title() {
      let t = ''
      if (this.doc && this.doc.docName) {
        t = this.doc.docName;
      }

      if (this.doc && this.doc.name) {
        t = this.doc.name;
      }

      document.title = t

      return t;
		},
		getPlanId() {
			return this.initParams.planId
    },
  },
  watch: {
		getPlanId(newPlanId) {
      console.log('take new fields', newPlanId)
			// this.getFields();
    },
    '$route' (to, from) {
      this.$router.go(0);
    },
    activeModel: {
      handler: function(newVal, oldVal) {
        let keys = Object.keys(newVal);
        keys.map((k,v) => {
          
          if(this.computeFields[k]) {
            // console.log(k, newVal[k])
            let c = this.computeFields[k]
            let diff = parseInt(newVal[k] || 0)
            if(c && c.initValue) {
              diff -= parseInt(c.initValue)
            }
            this.computeFields[k].initValue = parseInt(newVal[k] || 0)
            // console.log(this.activeModel[c.totalNodeId])
            if(this.activeModel[c.totalNodeId] != "undefined") {
              let oldTotal = parseInt(this.activeModel[c.totalNodeId] || 0)
              this.activeModel[c.totalNodeId] = oldTotal + diff
            }
          }
        })
      },
      deep: true
    }
	},
  beforeMount() {
    this.initUrlParams();
  },
  mounted() {
    // console.log("page inited", this.getDocUrl);
    this.loadDoc();

    window.execSubmit = this.submitHandler
    window.setOnlineDoc = this.setDocHandler
  },
  methods: {
    initUrlParams() {
      this.initParams.planId = this.$route.query.planId
      this.initParams.docId = this.$route.query.docId
      this.initParams.showTitle = (this.$route.query.showTitle || "false") == "true"
      this.initParams.showToolbar = (this.$route.query.showToolbar || "false") == "true"
      this.initParams.patientDocId = this.$route.query.patientDocId
      this.initParams.patientId = this.$route.query.patientId
      this.initParams.Online = (this.$route.query.Online || "true") == "true"
    },
    setDocHandler(doc) {
      this.doc = doc;
      this.initDoc();
    },
    loadDoc() {
      if(!this.initParams.Online) return;

      if(!this.initParams.planId) {
        this.initParams.planId = "default"
      }
      console.log("current open plain id", this.initParams.planId)
      if (!this.loading) {
        this.loading = this.$createToast({
          txt: "Loading...",
          mask: true,
          time: 1 * 60 * 1000,
        });
      }
      this.loading.show();
      axios
        .get(this.getDocUrl)
        .then((res) => {
         
          this.doc = res.data.data;
          
          this.initDoc();
          // this.fields = this.getFields();
          // console.log(res.data.data)

          this.loading.hide();
        })
        .catch((err) => console.log(err));
    },
    initDoc() {
      if(!this.doc || !this.doc.data) {
        console.log('doc data is null');
        return;
      }
      // this.initParams.planId= 'default'
      var temps = {};
      temps["default"] = this.doc.data;
      // console.log(shortKeys)
      var planCount = Object.keys(this.doc.plans||{}).length
      if(planCount == 0) {
        // this.initParams.showToolbar = false
      } else {
        var keys = Object.keys(this.doc.plans);
        var nums = []
        for(let i=0;i<keys.length;i++) {
          var key = keys[i]
          if(key.length == 7) {
            nums.push(parseInt(key.substring(0,1)))
          } else if(key.length == 8) {
            nums.push(parseInt(key.substring(0,2)))
          }
        }
        var sortNums = [...nums].sort((a,b)=>{return a-b;})

        // console.log(sortNums)
        // console.log(nums)
        var shortKeys = []
        for(let i = sortNums[0]; i <= sortNums[nums.length-1];i++) {
          shortKeys.push(keys[nums.indexOf(i)])
        }
        
        for(let i=0,key;key=shortKeys[i];i++) {
          temps[key] = this.doc.plans[key]
        }
      }

      this.plans = temps;
      
      this.initFields(this.initParams.planId || "default")
    },
		setField(nodeId, value) {
			let target = this.plans[this.initParams.planId].filter(s=>{
				return s.nodeId == nodeId
			})[0]
			if(target && value) {
				target.value = value;
				// console.log('set field ', target.name, value);
      }
      // console.log('nodeId',nodeId,"value", value)
		},
		initFields(planId) {
			var plan = this.plans[planId];
			// console.log(this.plan)
      var tempModel = {},tempFields = [], tempGroupFields = {}, tempComputeFields = {}, tempTotalMap = {}
			plan.map((item, index) => {
				if (item.name && (item.type == "input" || item.type == "text")) {

					tempModel[item.nodeId] = item.value;
					let f = {
						type: "input",
						modelKey: item.nodeId,
						label: item.name,
						props: {
							placeholder: "请输入",
							value: item.value,
							readonly: item.sourceId ? true : false,
						},
						rules: {
							required: true,
            },
            index: index
					};
					tempFields.push(f);
				} else if (item.name && (item.type == "totalscore")) {

          tempTotalMap[item.name] = item.nodeId
					tempModel[item.nodeId] = item.value;
					let f = {
						type: "input",
						modelKey: item.nodeId,
						label: item.name,
						props: {
							placeholder: "请输入",
							value: item.value,
							readonly: true,
						},
						rules: {
							required: true,
            },
            index: index
					};
					tempFields.push(f);
				} else if( item.name && item.type == "score") {
          
          if(item.attr && item.groupName && item.attr.score > -1) {
            let f = tempGroupFields[item.groupName];
            if(f) {
              f.options.push({
                value: item.attr.score,
                text: item.name,
                selected: item.value ? true: false
              })
              f.total = item.totalGroupName
              f.value = item.value ? item.value: null
            } else {
              tempGroupFields[item.groupName] = {
                key: item.groupName,
                options: [{
                  value: item.attr.score,
                  text: item.name,
                  selected: item.value ? true: false
                }],
                total: item.totalGroupName,
                index: index,
                value: item.value ? item.value: null
              }
            }
          } else {
            tempComputeFields[item.nodeId] = {
              total: item.totalGroupName,
              initValue: item.value
            }
            tempModel[item.nodeId] = item.value;
            let f = {
              type: "input",
              modelKey: item.nodeId,
              label: item.name,
              props: {
                placeholder: "请输入",
                value: item.value,
                readonly: item.sourceId ? true : false,
              },
              rules: {
                required: true,
              },
              index: index
            };
            tempFields.push(f);
          }
        } else if (
					item.type == "select" ||
					item.type == "radio" ||
					item.type == "checkbox"
				) {
					tempModel[item.nodeId] = item.value;
          // console.log(item.attr&&item.attr.options)
          // console.log(item.value)
					let f = {
						type: "select",
						modelKey: item.nodeId,
						label: item.name,
						props: {
							options: item.attr.options.map((s, i) => {
								return {
									value: s.value,
									text: s.label,
								};
              }),
              value: item.value,
							disabled: item.sourceId ? true : false,
						},
						rules: {
							required: true,
            },
            index: index
					};
					tempFields.push(f);
				} else if (item.type == "timeinput" || item.type == "datetime") {
					tempModel[item.nodeId] = item.value;
					let f = {
						component: DATE,
						modelKey: item.nodeId,
						label: item.name,
						props: {
							options:
								item.attr && item.attr.options
									? item.attr.options.map((s, i) => {
											return s.label;
										})
									: [],
							disabled: item.sourceId ? true : false,
						},
						rules: {
							required: true,
						},
						messages: {
							required: '请选择'
            },
            index: index
					};
					tempFields.push(f);
				} else if(item.type == "check") {
          // checkbox
          tempModel[item.nodeId] = item.value;
					let f = {
						type: "checkbox",
						modelKey: item.nodeId,
						label: '',
						props: {
							option: {
                value: item.value,
                label: item.name,
              },
              value: item.value,
						},
						rules: {
							required: false,
            },
            index: index
					};
					tempFields.push(f);
        }
      });
      console.log(tempGroupFields)
      let groupKeys = Object.keys(tempGroupFields);
      if(groupKeys && groupKeys.length > 0) {
        let items = []
        groupKeys.map((key, index) => {
          items.push(tempGroupFields[key])
        })
        items.sort((a,b)=>{return a.index - b.index})

        for(let k in tempComputeFields) {
          tempComputeFields[k].totalNodeId = tempTotalMap[tempComputeFields[k].total]
        }

        for(let i=0,item;item = items[i];i++) {
          tempComputeFields[item.key] = {
            total: item.total,
            initValue: item.value,
            totalNodeId: tempTotalMap[item.total]
          }
          tempModel[item.key] = item.value;
					let f = {
						type: "select",
						modelKey: item.key,
						label: item.key,
						props: {
							options: item.options.map((s, i) => {
								return {
									value: s.value,
									text: s.text,
								};
              }),
              value: item.value,
						},
						rules: {
							required: true,
            },
            index: item.index
					};
					tempFields.push(f);
        }
      }
      console.log(tempComputeFields)
			this.activeModel = tempModel
      this.activeField = tempFields.sort((a,b)=>{return a.index - b.index})
      this.computeFields = tempComputeFields
		},
    submitHandler(e) {
      if(this.submiting) {
        console.log('submiting....')
        return;
      }
      this.submiting = true
      this.loading.show()
      // console.log(this.activeModel);
      let keys = Object.keys(this.activeModel)
      keys.map((key,index)=>{
        this.setField(key, this.activeModel[key])
      })
      let tempData = []
      for(let key in this.plans) {
        tempData = tempData.concat(this.plans[key])
      }
      // console.log(tempData)
      this.doc.data = tempData
      delete this.doc.plans;
      // console.log(this.plans[this.initParams.planId])
      axios.post(this.saveUrl,this.doc).then(res=>{
        
        this.submiting = false
        this.loading.hide()
        this.$createToast({
          type: "correct",
          txt: "保存成功",
          time: 1000,
        }).show();

        try {
          android.onSaveResult(true, '保存成功')
        } catch (error) {
          console.error('android onSaveResult exec error', error)
        }

        if(this.initParams.docId) {
          let {patientDocId} = res.data.data
          this.initParams.docId = ""
          this.initParams.patientId = ""
          this.initParams.patientDocId = patientDocId
          this.$router.push({path:"default",query:this.initParams})
        }
      }).catch(err=>{
        this.submiting = false
        console.error(err)
        try {
          android.onSaveResult(false, '保存失败')
        } catch (error) {
          console.error('android onSaveResult exec error', error)
        }
      })
      console.log("submit");
    },
    validateHandler(result) {
			// console.log(result);
      // this.validity = result.validity;
      // this.valid = result.valid;
      // console.log(
      //   "validity",
      //   result.validity,
      //   result.valid,
      //   result.dirty,
      //   result.firstInvalidFieldIndex
      // );
    },
    fieldClickHandler(e) {
      console.log(e);
    },
    showDrawer() {
      this.$refs.drawer.refill(0, [
        {
          text: this.title || "文档1",
          value: "1",
        },
      ]);
      this.$refs.drawer.show();
    },
    changeHandler(index, item, selectedVal, selectedIndex, selectedText) {
      setTimeout(() => {
        // city change, get area data
        const data = Object.keys(this.plans).map((item, index) => {
          return {
            text: "第" + num2Chinese(index + 1) + "行",
            value: item,
          };
        });

        this.$refs.drawer.refill(index + 1, data);
      }, 200);
    },
    selectDrawerHandler(selectedVal, selectedIndex, selectedText) {
      // console.log(selectedVal)
      this.initParams.planId = selectedVal[1]
      this.$router.push({path:"default",query:this.initParams})
    },
    clearForm() {},
    cancelHandle() {
      this.$createToast({
        type: "correct",
        txt: "Picker canceled",
        time: 1000,
      }).show();
    },
    clickHandler(item) {
      if (item.action) {
        this[item.action](item);
      }
    },
  },
  components: {
    CubePage,
    CubeButtonGroup,
    DatePicker,
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  // .page-content
  //   padding-bottom: 52px
  
  .form-custom {
    .cube-form-item {
      .cube-btn {
        padding-left: 0;
        padding-right: 0;
        text-align: left;
        color: inherit;
        background: none;
        border: none;
      }
    }
  }

  .orange {
    color: #fc9153;
  }

  .cube-drawer-main {
    top: 54px !important;
    z-index: 5 !important;
  }

  .cube-drawer-item_active {
    color: #fc9153;
    background-color: transparent;
  }

  .cube-drawer-main {
    margin-bottom: 10px;
  }

  .cube-drawer-panel {
    width: 250px !important;
  }
  .cube-toolbar {
    right: 0 !important;
    left: 0 !important;
    bottom: 0 !important;
  }
</style>



