import Vue from 'vue'
import Router from 'vue-router'
import Default from '@/pages/Default'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: to => {
        return "/default"
      }
    },
    {
      path: '/default',
      name: 'Default',
      component: Default
    }
  ],
  mode: 'hash',
})



